module.exports = [{"reponse":"<p>Larticle 17 de la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales) prvoit que le contrat de travail n'est considr comme dfinitivement conclu qu' la fin de la priode d'essai dont la dure est de 30 jours.\nCependant, les stipulations introduites dans une convention collective avant le 1err juillet 2009, ce qui est le cas pour cet article 17, et qui fixent des dures d'essai plus courtes que les dures lgales ne sont plus applicables depuis cette date.\nOr, larticle L.1221-19 du code du travail fixe  2 mois la dure maximale de la priode dessai pour les ouvriers et les employs.</p><p>Par consquent, votre employeur est en droit de vous faire effectuer une priode dessai dune dure de 2 mois.</p><p>En raison de lexistence, dans certains dpartements, dune annexe  la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales), vous tes invit  consulter celle qui intresse ventuellement votre lieu de travail et qui peut contenir des stipulations diffrentes en matire de priode dessai.\nVous pouvez accder  la convention collective applicable  votre employeur ainsi qu ses annexes dpartementales sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"BOULANGERIES-PTISSERIES ARTISANALES","themes":[41,42,43],"question":"Je viens dtre embauch comme ptissier chez un boulanger-ptissier. Je suis mis  lessai pendant deux mois. On me dit que la convention collective de la boulangerie prvoit un maximum de 30 jours. Est-ce vrai?"},{"reponse":"<p>Non. Par drogation au droit commun qui ne prvoit quun seul contrat de travail par employeur, lassistant maternel est titulaire dautant de contrats de travail que denfants accueillis mme si ces derniers sont issus de la mme fratrie. Cette obligation de signer un contrat par enfant relve de larticle 4 de la convention collective nationale des assistants maternels du 1er juillet 2004, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 2395.</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"ASSISTANT.E.S MATERNEL.LE.S","themes":[41,44],"question":"Je fais garder mes deux enfants par le mme assistant maternel, puis-je tablir un seul contrat?"},{"reponse":"<p>Si le poste de chef mcanicien tait vacant ou cr par lemployeur avant cette nouvelle embauche, un appel  candidature interne aurait d tre ralis auprs de lensemble des salaris possdant les comptences requises pour occuper le poste en question et accder  la promotion. La convention collective prvoit que les vacances ou crations de poste doivent tre portes  la connaissance des salaris susceptibles d'tre intresss, en raison de leurs aptitudes (article 1.07 de la convention collective nationale du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 1090).</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"GARAGES","themes":[],"question":"Un nouveau salari a t embauch sur un poste de chef mcanicien. Nous sommes 3mcaniciens. Ce poste naurait-il pas d nous tre propos?"},{"reponse":"<p>La convention collective nationale des entreprises de propret et des services associs prvoit,  larticle 7.2 II, que le transfert des contrats de travail seffectue de plein droit et simpose donc au salari. Le maintien dans lemploi entraine la poursuite du contrat de travail au sein de lentreprise entrante.\nLentreprise entrante tablira un avenant au contrat de travail, pour mentionner le changement demployeur. Cet avenant reprendra lensemble des clauses du contrat de travail existant. Il  devra tre remis au plus tard 8 jours ouvrables aprs le dbut effectif des travaux.</p><p>Pour la rmunration, le salari bnficiera du maintien de sa rmunration mensuelle brute correspondant au nombre dheures habituellement effectues sur le march repris.  cette rmunration sajouteront les lments de salaire  priodicit fixe de manire  garantir le montant global annuel du salaire antrieurement peru correspondant au temps pass sur le march repris.\nPour lanciennet, le salari gardera celle acquise depuis le dbut du travail effectu sur le march avec lentreprise sortante.</p><p>Pour les congs pays acquis  la date du transfert, lentreprise entrante devra accorder aux salaris, qui en font la demande, la priode dabsence correspondant au nombre de jours de congs acquis dj indemniss par lentreprise sortante. Ces jours de congs dj indemniss par lancien employeur ne seront pas rmunrs par le nouveau.</p><p>Les salaris bnficieront du statut collectif en vigueur chez le nouvel employeur qui se substituera ds le premier jour de la reprise  celui du prcdent employeur.\nVous pouvez consulter la convention collective nationale des entreprises de propret sur le site internet <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a>; son numro didentification (IDCC) est le 3043.</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"ENTREPRISES DE PROPRET","question":"Je travaille dans le secteur de la propret. Que deviennent les dispositions de mon contrat de travail en cas de reprise du chantier sur lequel je suis affect?"},{"reponse":"<p>Dans la mtallurgie lorsquau cours des six mois prcdant lembauche, un ingnieur ou un cadre a occup, dans lentreprise, la mme fonction, dans le cadre dun ou plusieurs CDD ou dans celui dune ou plusieurs missions de travail temporaire, la dure de ces CDD et celles de ces missions sont dduites de la priode dessai prvue au contrat. En lespce seule la dure de votre CDD, doit tre dduite de la priode dessai dont la dure est ainsi ramene  un mois.</p><p>En ce qui concerne lanciennet, il doit tre tenu compte de la dure de tous les contrats de travail antrieurs avec cette entreprise mais aussi de la dure de toutes les missions  effectues dans lentreprise soit en lespce une dure totale de 5 mois.</p><p>(Articles 5 et 6 convention collective nationale des ingnieurs et cadres de la mtallurgie, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 650.)</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"MTALLURGIE","question":"Je suis ingnieur, je viens dtre embauch en CDI dans une entreprise de la mtallurgie dans laquelle jai termin il y a un mois un CDD de deux mois  la mme fonction. Il y a plus de six mois jai effectu une mission dintrim de 3 mois, mais sur un autre poste. Mon contrat comporte une clause dessai de trois mois. Ces priodes peuvent-elles tre dduites de la dure de la priode dessai prvue  mon contrat de travail  et compteront elles pour mon anciennet?"},{"reponse":"<p> En principe, comme vous tes  temps partiel, la rpartition de la dure du travail constitue un lment du contrat de travail qui ne peut tre modifi sans votre accord.</p><p>Toutefois, une clause de votre contrat de travail peut prvoir les cas et la nature des modifications de la rpartition de la dure du travail entre les jours de la semaine et les semaines du mois. Dans ce cas vous pouvez quand mme refuser la modification lorsquelle nest pas compatible avec des obligations familiales imprieuses, avec le suivi dun enseignement scolaire ou suprieur, une priode dactivit fixe chez un autre employeur, ou une activit professionnelle non salarie.</p><p>A  moins quelle nintervienne dun commun accord, la modification de vos horaires doit vous tre notifie deux semaines au moins avant la date  laquelle elle doit avoir lieu (article 6-3.3 de la convention collective du commerce de dtail et de gros  prdominance alimentaire, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 2216.).</p><p>Un accord dentreprise ou dtablissement peut prvoir des dispositions diffrentes. Il est en consquence ncessaire de vrifier lexistence ventuelle dun tel accord sur le sujet.\nVous pouvez  cet effet vous rapprocher des services de lunit dpartementale de la DIRECCTE de votre lieu de travail.</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"COMMERCES  PRDOMINANCE ALIMENTAIRE","question":"Je suis  temps partiel, mon employeur peut-il modifier la rpartition de ma dure du travail?"},{"reponse":"<p>Oui, des amnagements peuvent tre expressment  prvus pour modifier la dure du travail  de ces tudiants afin de permettre notamment de concilier les exigences lies  la poursuite dtude et  lactivit professionnelle. Il est ncessaire dans ce cas de rdiger un avenant  leur contrat de travail.\nToutefois, si le recours  des heures complmentaires a pour effet de porter la  dure du travail de ces salaris au-del de 35 heures hebdomadaires  pendant une priode dun mois le contrat sera rput conclu  temps complet.\nIl est  noter galement que la dure minimale de 24h nest pas applicable  ces contrats  temps partiel (article 6-1.2 de la convention collective nationale du commerce de dtail et de gros  prdominance alimentaire, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 2216).</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"COMMERCES  PRDOMINANCE ALIMENTAIRE","question":"Jai embauch des jeunes avec des contrats  temps partiels tudiants. Puis je modifier leur dure du travail contractuelle de base, notamment pendant les vacances scolaires et universitaires?"},{"reponse":"<p>Larticle 12-4 de la convention collective nationale des ouvriers du Btiment prvoit la prise en considration du diplme professionnel pour la dtermination de la position du salari dans la classification conventionnelle des emplois. Par exemple, si vous tes titulaire dun CAP dlectricien, vous devez tre class au niveau II, coefficient 185.</p><p>A l'issue d'une priode maximale de 9 mois, vous serez soit confirm dans cette  position ou class  un niveau suprieur en fonction de vos aptitudes et capacits professionnelles. Ce dlai est rduit  6 mois pour les ouvriers ayant une exprience antrieure d'entreprise, acquise notamment par l'apprentissage ou par la formation en alternance.\nVous pouvez consulter le texte des conventions collectives sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>:</p><p>Convention collective nationale des ouvriers employs par les entreprises du btiment occupant jusqu 10 salaris: identifiant IDCC 1596\nConvention collective nationale des ouvriers employs par les entreprises du btiment occupant plus de 10 salaris: identifiant IDCC 1597</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"BTIMENT","question":"Embauch rcemment dans une entreprise du Btiment en tant qulectricien, je souhaite savoir si lemployeur doit prendre en compte mon diplme professionnel pour la dfinition de mon coefficient demploi?"},{"reponse":"<p>Lembauche de tout salari  y compris un extra - ne peut intervenir quaprs dclaration nominative auprs de lURSSAF (dclaration pralable  lembauche  DPAE). Un rcpiss DPAE peut tre remis au salari (article R. 1221-9 du code du travail). Celui-ci doit tre enregistr dans le registre unique du personnel.</p><p>Un contrat de travail obligatoirement crit est sign par chacune des parties, et remis au salari dans les 2 jours suivant lembauche.</p><p>Il doit prciser lobjet du contrat (ex: saison), la date de fin du contrat ou sa dure minimale, la dure de la priode dessai - 1 jour par semaine prvue au contrat dans la limite de 2 semaines si le contrat est  infrieur ou gal  6 mois.</p>","theme":"CONTRAT DE TRAVAIL ET EMBAUCHE","branche":"HOTELS-CAFS-RESTAURANTS","question":"Je souhaite embaucher des salaris pour la saison, quelles sont les formalits dembauche  accomplir?"},{"reponse":"<p>La convention collective nationale de la boulangerie - ptisserie (entreprises artisanales) prvoit le versement dune  indemnit de frais professionnels qui est couramment appele prime de panier ou prime casse-crote. Cependant, cette indemnit ne concerne que les ouvriers boulangers et ouvriers ptissiers qui ne sont pas nourris par leur employeur (article 24 de la convention collective).</p><p>Il a t jug que cette indemnit est galement due aux apprentis boulangers ou ptissiers non nourris lorsque ceux-ci ont t embauchs selon les mmes conditions de travail que les autres salaris de lentreprise (Cour de cassation, chambre sociale, 19 octobre 2005, n03-44.288)</p><p>De ce fait, si tel est votre cas et que votre employeur ne vous nourrit pas, vous devez percevoir cette indemnit journalire gale  une fois et demie le minimum garanti, soit 5,31 euros depuis le 1erjanvier 2017.\nEn raison de lexistence, dans certains dpartements, dune annexe  la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales), vous tes invit  consulter celle qui intresse ventuellement votre lieu de travail et qui peut contenir des dispositions diffrentes.</p><p>Vous pouvez accder  la convention collective applicable  votre employeur ainsi qu ses annexes dpartementales sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"SALAIRE","branche":"BOULANGERIES-PTISSERIES ARTISANALES","question":"Je suis apprenti boulanger chez un boulanger. Ai-je le droit  la prime casse-crote?"},{"reponse":"<p>Le dernier accord portant sur les salaires minima, qui a t conclu dans le cadre de la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales), fixe le salaire horaire minimum brut  10,32 euros pour votre coefficient  (avenant n 116 du 16 janvier 2017).\nVous percevez donc  lheure actuelle un salaire horaire suprieur au salaire minimum conventionnel qui vous est d en fonction de votre classement au coefficient 175.</p><p>Par consquent, votre employeur nest pas tenu daugmenter le taux horaire de votre salaire.\nCeci tant, il sagit l du montant du salaire horaire. Le fait que celui-ci soit actuellement suprieur au salaire horaire minimum professionnel ne dispense pas lemployeur de lobligation de rmunrer la totalit des heures de travail effectif rellement accomplies au cours du mois, en appliquant le cas chant le taux de majoration pour les ventuelles heures supplmentaires.</p><p>Vous tes donc invit  vrifier que le nombre dheures de travail figurant sur vos bulletins de paie correspond bien au temps de travail que vous avez effectu dans le mois concern.\nSi toutes les heures travailles ne vous semblent pas mentionnes, vous devez en demander le paiement  votre employeur, lequel doit tenir un dcompte prcis de votre temps de travail effectif conformment aux exigences de larticle D. 3171-8 du code du travail. En cas de refus de sa part, vous pouvez saisir linspection du travail de ce diffrend avant, si besoin, de vous adresser au conseil de prudhommes.</p><p>Vous pouvez accder  la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales) sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"SALAIRE","branche":"BOULANGERIES-PTISSERIES ARTISANALES","question":"Je suis le seul ouvrier boulanger dans la boulangerie-ptisserie, ce qui mamne  travailler normment. Je suis pay 14 euros de lheure depuis des annes et mon employeur refuse daugmenter mon salaire. Mon coefficient est le 175."},{"reponse":"<p> Il nest possible dtre rmunr aux heures relles que dans le cadre dun contrat de travail dont la dure est infrieure  un mois. A dfaut, la mensualisation du salaire tant obligatoire, vous devez tre pay tous les mois sur un salaire de base mensuel identique. Le salaire mensuel brut de base sera ainsi dtermin en application de larticle 5 de la convention collective nationale des assistants maternels:</p><ul>\n<li>Si l'accueil s'effectue sur une anne complte (52 semaines y compris les congs pays du salari), le salaire mensuel brut de base est gal :</li>\n</ul><p><code>(salaire horaire brut de base  nombre d'heures d'accueil par semaine  52 semaines)</code></p><ul>\n<li>Si l'accueil s'effectue sur une anne incomplte (semaines programmes hors congs annuels du salari), le salaire mensuel brut de base est gal :</li>\n</ul><p><code>(Salaire horaire brut de base x nb dheures daccueil par semaine x nb de semaines programmes)</code></p><p>En cas de mois comportant des absences non rmunres, il convient de rmunrer les heures travailles sur la base du taux horaire rel dabsence calcul en appliquant la mthode dfinie par la Cour de Cassation: le taux horaire rel dabsence est gal au salaire mensuel de base divis par le nombre dheures qui auraient rellement t travailles dans le mois considr sil ny avait pas eu dabsence; il ne sagit donc pas du taux horaire contractuel (Cassation, chambre sociale, 20 janvier 1999, n 96-45042).</p>","theme":"SALAIRE","branche":"ASSISTANT.E.S MATERNEL.LE.S","question":"Mon salaire varie tous les mois en fonction du nombre dheures que jeffectue, est-ce lgal?"},{"reponse":"<p>La clause dobjectifs fixe au salari un objectif  atteindre (par exemple, quotas de vente, chiffre daffaires). Lemployeur doit veiller  ce que ces objectifs soient ralistes et compatibles avec le march. Il faut galement que le salari ait les moyens de raliser les objectifs fixs. En cas de litige, lemployeur devra justifier quil lui avait bien donn les moyens daccomplir son travail.\nAinsi, lemployeur qui met le salari dans limpossibilit datteindre les objectifs fixs (objectifs irralisables, absence de moyens) risque de devoir lui payer des dommages et intrts (arrt de la Cour de cassation du 5 avril 2012).</p><p>Le contrat de travail peut prvoir que les objectifs soient dfinis priodiquement par accord entre lemployeur et le salari. En cas de dsaccord, lemployeur doit alors sen tenir aux objectifs fixs antrieurement. Sil cesse de verser la rmunration variable au motif quaucun accord na t trouv avec le salari, ce dernier pourra alors saisir le juge qui fixera les objectifs par rfrence aux annes antrieures.</p><p>Le contrat de travail peut galement prvoir quil revient  lemployeur de fixer les objectifs dans le cadre de son pouvoir de direction. Dans ce cas, il peut les modifier  deux conditions: ils doivent tre ralisables et ils doivent avoir t ports  la connaissance du salari en dbut dexercice. Si ces deux conditions sont remplies, lemployeur na pas  obtenir laccord pralable du salari, mme si cette modification a un impact sur sa rmunration. Les objectifs fixs par lemployeur simposeront au salari.\nSi le salari natteint pas ses objectifs, lemployeur peut envisager un licenciement mais seulement si les objectifs taient raisonnables et si lincapacit du salari  les atteindre rsulte dune insuffisance professionnelle ou dune faute de celui-ci.</p><p>En revanche, lemployeur ne peut pas infliger une sanction pcuniaire au salari qui natteint pas ses objectifs dans la mesure o une telle sanction est interdite (article L. 1331-2 du code du travail).</p>","theme":"SALAIRE","branche":"GARAGES","question":"Je suis commercial dans une concession automobile, mon employeur me fixe de nouveaux objectifs avec lesquels je suis en dsaccord et jai des pnalits si je ne les atteins pas, que puis-je faire?"},{"reponse":"<p>La continuit du service ou la scurit des usagers, dans le dpannage-remorquage, sont une justification au recours au travail de nuit selon la convention collective du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes (article 1.10).</p><p>Si le salari est identifi comme travailleur de nuit dans son contrat de travail, il doit percevoir pour chaque heure travaille au cours de la priode nocturne (entre 21h et 6h ou 22h et 7h aprs consultation des reprsentants  du personnel) une majoration de 10% du minimum conventionnel mensuel applicable divis par 151,66.</p><p>En outre, le travailleur de nuit doit bnficier dun repos fix  1,66% au titre de chaque heure effectue pendant la priode nocturne (pris pour moiti  linitiative de lemployeur et pour moiti  celle du salari).\nSi le salari ne travaille quexceptionnellement de nuit, pour chaque heure travaille au cours de la priode nocturne, il doit percevoir une majoration de 50% du salaire horaire brut de base (majoration sajoutant  celles pour heures supplmentaires).</p><p>Une indemnit de panier de nuit est verse  tout salari, reconnu travailleur de nuit ou non, qui accomplit au moins 2 heures de travail au cours de la priode nocturne. Elle peut tre affecte  un compte pargne temps (montant rvis chaque anne par avenant conventionnel).\nVous pouvez consulter la convention collective nationale du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes, sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 1090.</p>","theme":"SALAIRE","branche":"GARAGES","question":"Je suis dpanneur, je suis amen  intervenir la nuit sans majoration de salaire. Est-ce normal?"},{"reponse":"<p>La convention collective nationale des entreprises de propret et services associs dfinit le statut de travailleur de nuit.</p><p>Un salari est considr comme travailleur de nuit sil accomplit au moins 2 fois par semaine, selon son horaire habituel, au moins 3 heures de son temps de travail quotidien inscrit au contrat durant la priode comprise entre 21 heures et 6 heures.</p><p>Sont aussi considrs comme travailleur de nuit les salaris qui accomplissent pendant une priode de 12mois conscutifs, 270 heures de travail pendant la plage horaire de nuit.</p><p>1) La compensation salariale attribue aux salaris qui ont le statut de travailleur de nuit est la suivante (article 6.3.5 de la convention collective nationale des entreprises de propret):</p><p>Les heures de travail effectues entre 21heures et 6 heures sont majores dans les conditions suivantes:</p><ul>\n<li>travaux rguliers: 20%</li>\n<li>travaux occasionnels: 100%</li>\n</ul><p> savoir, il y a aussi un repos compensateur attribu au travailleur de nuit de 2% du travail effectif accompli entre 21 heures et 6 heures dans le mois.</p><p>2) La compensation salariale attribue aux salaris nayant pas le statut de travailleur de nuit est la suivante (article 6.3.4 de la convention collective nationale des entreprises de propret et services associs):</p><p>Les heures effectues entre 21 heures et 5 heures sont majores dans les conditions suivantes:</p><ul>\n<li>travaux rguliers: 20%</li>\n<li>travaux occasionnels: 100%</li>\n</ul><p>Enfin, une prime de panier gale  2 fois le minimum garanti (qui est de 3,54 euros depuis le 1er janvier 2017) est accord aux personnels effectuant au moins 6h30 au cours de la vacation.</p><p>Vous pouvez consulter la convention collective nationale des entreprises de propret et services associs, sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 3043.</p><p>Ces lments sur le travail de nuit sappliquent sous rserve quil ny ait pas daccord dentreprise comportant des dispositions plus ou moins favorables. Vous pouvez interroger sur ce point vos reprsentants du personnel ou vous rapprocher des services de lunit dpartementale de la DIRECCTE de votre lieu de travail.</p>","theme":"SALAIRE","branche":"ENTREPRISES DE PROPRET","question":"Je travaille dans le secteur de la propret. Ai-je droit  des majorations de salaire si je travaille de nuit?"},{"reponse":"<p>Ce nest pas la loi qui fixe les salaires minima; seul le SMIC (Salaire Minimum Interprofessionnel de Croissance) est fix par dcret, le 1er Janvier de chaque anne.\nIl convient de se reporter  la convention collective de la Mtallurgie.\nDans cette branche professionnelle, il existe tout dabord des accords nationaux tendus, autrement dits applicables  lensemble de la branche professionnelle de la Mtallurgie; entre autres domaines, figure la classification des emplois.</p><p>Si, dans votre dpartement, il existe par ailleurs une convention collective dpartementale tendue, autrement dit applicable  lensemble du secteur de la Mtallurgie du dpartement, il convient galement de sy reporter au cas o elle pourrait ventuellement contenir des dispositions plus favorables.</p><p>Ceci afin de vrifier, compte tenu de votre emploi rellement occup, si la classification porte sur votre bulletin de salaire est bien conforme.</p><p>Dans un second temps, il convient de se reporter  la grille des salaires correspondante;  dans la Mtallurgie, les accords de salaires sont conclus au niveau dpartemental. Ils fixent, en fonction de chaque classification, le salaire minimal correspondant.</p><p>Pour autant, si certains minima taient devenus infrieurs au Smic, cest ce dernier qui devra sappliquer.</p><p>Il est  noter enfin quun tel accord nest rendu obligatoire pour lensemble des entreprises entrant dans son champ dapplication que lorsquil a fait lobjet dun arrt dextension pris par le Ministre du Travail.</p><p>Vous pourrez vrifier sil existe des textes conventionnels conclus localement en vous rapprochant des services de la DIRECCTE (unit dpartementale) de votre lieu de travail.</p>","theme":"SALAIRE","branche":"ENTREPRISES DE PROPRET","question":"Je suis ouvrier dans une entreprise de mtallurgie, et je voudrais savoir comment procder afin de vrifier, concernant mon salaire, si le minimum lgal est respect."},{"reponse":"<p>Les rmunrations minimales pour les ouvriers et les ETAM applicables dans le secteur de la mtallurgie sont consultables sur notre site internet:</p><p><a href=\"http://auvergne-rhone-alpes.direccte.gouv.fr/sites/auvergne-rhone-alpes.direccte.gouv.fr/IMG/pdf/metallurgie_oetam.pdf\">http://auvergne-rhone-alpes.direccte.gouv.fr/sites/auvergne-rhone-alpes.direccte.gouv.fr/IMG/pdf/metallurgie_oetam.pdf</a></p><p>A ces rmunrations sajoutent des majorations pour anciennet  partir de 3 ans (3% plus 1% par anne supplmentaire avec un maximum de 15% pour 15 ans danciennet) (cf. art. 19 de la convention collective de la mtallurgie de la Loire et de larrondissement dYssingeaux).</p><p>La prime danciennet ne se calcule pas sur le salaire rel mais sur la base suivante:\n(Valeur du point (4,29 euros au 1/06/2016)) X coefficient) major de 5% pour les ouvriers ou de 7% pour les techniciens datelier.\nLa prime danciennet est calcule en fonction de lhoraire effectif, subit les majorations pour heures supplmentaires et figure de faon distincte sur le bulletin de salaire.</p>","theme":"SALAIRE","branche":"ENTREPRISES DE PROPRET","question":"Je suis salari dune entreprise de mtallurgie dans la Loire (coefficient 270) et je souhaiterais avoir des informations sur les salaires applicables dans ce secteur dactivit et connatre le mode de calcul de la prime danciennet."},{"reponse":"<p>La convention collective du commerce de dtail et de gros  prdominance alimentaire opre une distinction entre le travail occasionnel ou exceptionnel et le travail rgulier ou habituel.\nSi vous travaillez un dimanche de manire occasionnelle ou exceptionnelle  chaque heure devra tre majore de 100% du salaire venant sajouter  la rmunration mensuelle (article L. 3132-13 du code du travail).</p><p>Si vous travaillez habituellement le dimanche au sein dun commerce de dtail dune surface de vente infrieure ou gale  400m2 et ne bnficiez pas dun jour et demi de repos conscutifs dans la semaine vous aurez droit  une majoration de 20%.</p><p>Si vous travaillez habituellement le dimanche au sein dun commerce de dtail dune surface de vente suprieure  400m2, vous aurez droit  une majoration de 30% pour les heures accomplies le dimanche jusqu 13h.</p><p>Vous pouvez consulter la convention collective nationale du commerce de dtail et de gros  prdominance alimentaire sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 2216.</p><p>Un accord dentreprise ou dtablissement ou un accord de branche peuvent en outre prvoir des dispositions diffrentes. Il est en consquence ncessaire de vrifier lexistence ventuelle dun accord dentreprise sur le sujet. Vous pouvez interroger sur ce point vos reprsentants du personnel ou vous rapprocher des services de lunit dpartementale de la DIRECCTE de votre lieu de travail.</p>","theme":"SALAIRE","branche":"COMMERCES  PRDOMINANCE ALIMENTAIRE","question":"Je suis salarie et mon employeur me demande de venir travailler le dimanche, ai-je droit  une majoration de salaire?"},{"reponse":"<p>La convention collective nationale des ouvriers des entreprises du Btiment prvoit, au profit des ouvriers non sdentaires du btiment, quils soient employs directement par lentreprise prestataire ou quils soient intrimaires, des indemnits en remboursement des frais engags pour les dplacements qu'ils effectuent quotidiennement pour se rendre sur le chantier et en revenir. Ces indemnits sont:</p><ul>\n<li>Une indemnit de repas due  tout ouvrier contraint de prendre son repas sur le chantier;</li>\n<li>Une indemnit de trajet due en contrepartie de la sujtion quotidienne pour le salari de se rendre sur les chantiers;</li>\n<li>Une indemnit de transport en remboursement des dpenses engages par louvrier pour se rendre sur les chantiers et en revenir.</li>\n</ul><p>Ces indemnits de remboursement de frais sont journalires, forfaitaires et fixes en valeur absolue.\nLe montant des indemnits de trajet et de transport sont fixs forfaitairement sur la base dun systme de 5 zones circulaires concentriques, contrairement  lindemnit de repas identique pour toutes les zones concentriques. Pour chaque entreprise, le point de dpart des petits dplacements, c'est--dire le centre des zones concentriques, est fix  son sige social. Les montants des indemnits de petits dplacements auxquels l'ouvrier bnficiaire a droit sont ceux de la zone dans laquelle se situe le chantier sur lequel il travaille.</p><p>Les montants de ces indemnits de petits dplacements sont fixs par les partenaires sociaux, organisations patronales et syndicales, au niveau de chaque rgion. Ils sont disponibles sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>. Vous pouvez aussi vous informer auprs du service de renseignements en droit du travail de lunit dpartementale de la DIRECCTE du dpartement dans lequel se situe votre employeur.</p>","theme":"SALAIRE","branche":"BTIMENT","question":"Ouvrier dune entreprise du BTP, travaillant sur chantiers, quelles sont les indemnits de dplacements auxquelles je peux prtendre?"},{"reponse":"<p> En cas darrt de travail pour intempries dcid par lemployeur, le salari a droit  lindemnisation pour intempries prvue par les articles L. 5424-6 et suivants du code du travail, pour chaque heure de travail perdue,  partir de la deuxime heure au cours dune mme semaine ou dune priode continue darrt.</p><p>Sont considres comme intempries, les conditions atmosphriques et les inondations lorsqu'elles rendent dangereux ou impossible l'accomplissement du travail eu gard soit  la sant ou  la scurit des salaris, soit  la nature ou  la technique du travail  accomplir (article L. 5424-8 du code du travail).</p><p>Lindemnisation pour intempries est due au salari, sous rserve quil remplisse les conditions suivantes:</p><ul>\n<li>tre prsent sur le chantier au moment de larrt de travail,</li>\n<li>justifier dau moins 200 heures de travail dans le BTP au cours des deux mois prcdant larrt de travail,</li>\n<li>ne pas avoir t indemnis plus de 55 jours depuis le 1er janvier de lanne (articles D. 5424-11  D.5424-14 du code du travail).</li>\n</ul><p>Le montant de lindemnisation slve  75% de la rmunration perue par le salari  la veille de linterruption (articles D. 5424-13 et D. 5424-15 du code du travail).</p><p>Le salari reste  la disposition de lentreprise pendant toute la priode dinactivit du chantier (article D.5424-18 du code du travail).</p><p>Les heures de travail effectues ultrieurement en remplacement des heures perdues sont rmunres sans tenir compte de lindemnisation susvise (article D. 5424-17 du code du travail).</p>","theme":"SALAIRE","branche":"BTIMENT","question":"En cas de cessation de travail due aux intempries, quels sont mes droits en matire de rmunration?"},{"reponse":"<p>Selon larticle 3b de la Directive europenne 2002-15 du 11 mars 2002, si le temps dattente est connu par le salari et quil peut vaquer  des occupations personnelles sans devoir rester  proximit du camion, dans ce cas ce temps dattente est considr comme un temps de repos donc non rmunr.</p><p>Si au contraire le salari ne connat pas la dure de lattente et doit tre disponible pour rpondre  des appels ventuels lui demandant d'entreprendre ou de reprendre la conduite ou de faire d'autres travaux, alors ce temps fait partie du temps de travail effectif rmunr au titre de la disponibilit selon la directive.</p>","theme":"SALAIRE","branche":"TRANSPORTS ROUTIERS","question":"Je suis en attente sur un site pour dcharger ma marchandise, est-ce que ce temps dattente doit tre pay?"},{"reponse":"<p>La dure conventionnelle du travail hebdomadaire des cuisiniers tant de 43 heures par semaine, le salaire se calcule comme suit:</p><p>43 heures X 4.33 (nombre moyen de semaines dans un mois) X 9,77 (taux horaire minimum conventionnel au 1er janvier 2017).</p><p>Le salaire inclut en outre un avantage en nature nourriture calcul comme suit: nombre de repas fournis X 3,54  (Minimum Garanti au 1er janvier 2017), et peut parfois inclure un avantage en nature logement.</p><p>Pour plus de prcisions, vous pouvez consulter la convention collective nationale des htels, cafs restaurants du 30 avril 1997, sur le site internet de Lgifrance: <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a>. Son code didentification (IDCC) est le 2216.</p>","theme":"SALAIRE","branche":"HOTELS-CAFS-RESTAURANTS","question":"Je suis cuisinier, comment se calcule mon salaire?"},{"reponse":"<p>Le salaire qui est vers au salari inclut une indemnit de congs pays dont le montant est gal  10% de la rmunration brute.</p><p>(Rfrences: article 16 e) de la Convention collective nationale des salaris du particulier employeur, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a> Son code didentification (IDCC) est le 2111)</p>","theme":"SALAIRE","branche":"PARTICULIERS EMPLOYEURS","question":"Comment sont rmunrs les congs pays avec le CESU?"},{"reponse":"<p>Larticle L. 3123-27 du code du travail fixe une dure minimale de travail pour les salaris  temps partiel: 24 heures par semaine ou son quivalent mensuel, cest--dire 104 heures par mois.</p><p>La convention collective nationale de la boulangerie - ptisserie (entreprises artisanales) a raffirm la volont de votre profession dobserver cette garantie minimale. Toutefois, elle prvoit que les employeurs qui ne pourraient offrir cette garantie  leurs salaris  temps partiel, peuvent conclure des contrats pour une dure de travail infrieure, en prenant l'engagement formel de leur garantir des horaires de travail rguliers.</p><p>Pour les entreprises de moins de 10 salaris, la dure minimale de travail pour le personnel de vente est ainsi ramene  6 heures par semaine, rparties sur deux jours au plus (article 21 de la convention collective).</p><p>Vous pouvez consulter cette convention collective sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"DURE DU TRAVAIL","branche":"BOULANGERIES-PTISSERIES ARTISANALES","question":"Je suis artisan boulanger et jemploie actuellement deux salaris. Je souhaite embaucher sous CDI une vendeuse  temps partiel. Suis-je tenu de lemployer pendant un nombre minimum dheures par semaine?"},{"reponse":"<p>Pour bnficier de la compensation en repos prvue par larticle 23 de la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales), vous devez tre reconnu comme tant un travailleur de nuit. Pour tre considr comme travailleur de nuit selon cette convention collective, le salari doit:</p><ul>\n<li>soit accomplir au moins 2 fois par semaine, selon son horaire de travail habituel, au minimum 3heures de travail quotidien entre 21 heures et 6 heures;</li>\n<li>soit accomplir au moins 270 heures de travail de nuit (toute heure de travail effectue entre 21heures et 6heures) dans lanne civile.</li>\n</ul><p>Si vous remplissez lune ou lautre de ces conditions, vous devez bnficier, en plus de la majoration de votre salaire horaire, dune compensation en repos gale :</p><ul>\n<li>une journe si, dans l'anne civile, vous avez effectu au moins 270heures de travail de nuit;</li>\n<li>deux journes si vous avez effectu plus de 600 heures de nuit.</li>\n</ul><p>Ce temps de repos doit alors tre pris au plus tard dans les 3 mois qui suivent lanne civile.\nVous pouvez consulter cette convention collective sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"DURE DU TRAVAIL","branche":"BOULANGERIES-PTISSERIES ARTISANALES","question":"Je travaille comme ouvrier boulanger dans une petite boulangerie, je commence mes journes certaines fois  3 heures du matin et dautres  5 heures. Les heures que jeffectue la nuit sont payes avec un supplment de 25%, mais jai entendu dire quelles donneraient droit aussi  des jours de repos."},{"reponse":"<p>En application de larticle L. 423-2 du code de laction sociale et des familles, la rglementation relative au temps partiel prvue par le code du travail (qui fixe un seuil de 24 heures minimum  dfaut daccord de branche) ne sapplique pas aux assistants maternels, et la convention collective nationale des assistants maternels ne fixe pas de dure minimale hebdomadaire de travail.</p>","theme":"DURE DU TRAVAIL","branche":"ASSISTANT.E.S MATERNEL.LE.S","question":"Y-a-t-il une dure minimale prvue pour une embauche dun assistant maternel  temps partiel cest--dire travaillant moins de 45 heures par semaine?"},{"reponse":"<p>Le principe pos par larticle 1.15 de la convention collective du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes est la prise de quatre semaines au titre du cong principal attribu, soit par fermeture de ltablissement, soit par roulement.</p><p>Ce cong principal de quatre semaines peut tre fractionn par lemployeur avec lagrment du salari, en deux ou plusieurs tranches, mais  la condition que lune delles soit suprieure ou gale  18 jours ouvrables.</p><p>Le salari peut exiger de prendre la tranche de 18 jours entre le 1er mai et le 31 octobre de lanne en cours.</p><p>Il ressort donc de ces dispositions conventionnelles que le salari doit bnficier, a minima, de 18 jours ouvrables, soit 3 semaines de congs pays au titre du cong principal.\nLemployeur est tenu de respecter ces dispositions; en effet, ce dernier est li par les clauses de la convention collective applicable  son entreprise.</p><p>En cas de non-respect de ces dispositions, le salari peut rclamer des dommages et intrts devant la juridiction prudhomale en rparation du prjudice subi.</p><p>Vous pouvez consulter la convention collective nationale du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes, sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 1090.</p>","theme":"DURE DU TRAVAIL","branche":"GARAGES","question":"Mon employeur ne me permet que 15 jours pour les congs dt, quels sont mes droits?"},{"reponse":"<p>Cette dure est fixe, conformment  larticle 6.2.4.1 de la convention collective des entreprises de propret et en vertu des dispositions de larticle L. 3123-19 du code du travail,  16 heures par semaine ou 69 h 28 par mois. Une dure infrieure peut tre prvue sur demande crite et motive du salari. En contrepartie de ce rgime drogatoire du droit commun, le salari bnficiera dune garantie dhoraires rguliers se reproduisant  lidentique dune semaine sur lautre et dun dlai de prvenance de 8 jours ouvrs en cas de modification des horaires. En application de larticle. 6.2.4.1 b de la convention collective, un regroupement dhoraires sera appliqu au salari  temps partiel. Ce regroupement des horaires de travail du salari pourra,  dfaut d'accord d'entreprise prvoyant des modalits de regroupement diffrentes, s'oprer sur 10 demi-journes rgulires au maximum par semaine, sauf volont expresse du salari. La demi-journe comprend un temps de travail rmunr  minima une heure par vacation. La demi-journe peut comporter deux vacations au maximum, le nombre de vacations tant fix en fonction de la dure du travail du salari.</p><p>Le tableau ci-dessous rcapitule le nombre de vacations et lamplitude journalire maximale du salari  temps partiel en fonction de sa dure du travail.</p><p>Sauf volont expresse du salari, la rpartition du nombre de vacations et des amplitudes est fixe en fonction de la dure du travail, ainsi:</p><table>\n<thead>\n<tr>\n<th align=\"center\">-Dure hebdomadaire (d)</th>\n<th align=\"center\">Nombre de vacations</th>\n<th align=\"center\">- Amplitude journalire maximale</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">-d &#x3C; 16 heures</td>\n<td align=\"center\">-2 au maximum</td>\n<td align=\"center\">12 heures</td>\n</tr>\n<tr>\n<td align=\"center\">16h &#x3C;-d &#x3C; 24h</td>\n<td align=\"center\">2 au maximum</td>\n<td align=\"center\">13 heures</td>\n</tr>\n<tr>\n<td align=\"center\">-d > 24 heures</td>\n<td align=\"center\">-3 au maximum</td>\n<td align=\"center\">13 heures</td>\n</tr>\n</tbody>\n</table><p>Vous pouvez consulter la convention collective nationale des entreprises de propret et services associs, sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 3043.</p>","theme":"DURE DU TRAVAIL","branche":"ENTREPRISES DE PROPRET","question":"Quelle est la dure minimale de travail pour les salaris  temps partiel dans le secteur de la propret?"},{"reponse":"<p>Pour ce qui est des chantiers de nuit: la convention collective Nationale des entreprises de Propret (articles. 4.3.2 et 6.3), interdit le travail aux mineurs (de 16  18 ans) sur la plage horaire 21h-6h.\nLe code du travail prvoit une liste dactivits dans lesquelles lune drogation peut est possible pour les jeunes travailleurs  lintrieur de cette priode, mais le secteur de la propret nen fait pas partie.</p><p>Pour ce qui est du travail le dimanche, le mme article prvoit galement linterdiction pour les jeunes travailleurs.</p><p>Par ailleurs, en tant que jeune travailleurs vous avez droit  un repos quotidien qui ne peut tre infrieur  12 heures, et  2 jours de repos conscutifs par semaine (article 4.3.2 de la convention collective).</p><p>Vous pouvez consulter la convention collective nationale des entreprises de propret et services associs, sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 3043.</p>","theme":"DURE DU TRAVAIL","branche":"ENTREPRISES DE PROPRET","question":"Je nai pas encore 18 ans, est ce que je peux tre affect la nuit sur des chantiers? Et le dimanche?"},{"reponse":"<p>L'employeur peut procder  des changements dhoraires dans l'entreprise sans obtenir laccord pralable du comit d'entreprise si cette modification n'est que ponctuelle ou ne concerne qu'un salari, et n'affecte pas les conditions gnrales de travail dans l'entreprise.\nAinsi, si les horaires individualiss appliqus dans l'entreprise sont amnags du lundi au vendredi, l'employeur peut amnager les horaires d'un salari du mardi au samedi si celui-ci est affect  des tches d'entretien et de rparation dont certaines ne peuvent s'effectuer que le samedi: arrt de la Cour de Cassation, chambre sociale, du 1er juill. 1997, n 95-12.000, n 2871 P + B.</p><p>Il peut, dans les mmes conditions, modifier lhoraire initial, si cela ne change pas les horaires dfinis par votre contrat de travail ou ne modifie pas la dure du travail hebdomadaire ou le salaire.</p><p>En lespce, votre contrat de travail joint en copie  vos questions, prvoit bien que votre horaire peut tre modifi afin de rpondre  la charge de travail fluctuante sans que cela puisse tre considr comme une modification du contrat de travail, laquelle supposerait alors votre accord.</p><p>Si lhoraire est collectif, lemployeur doit lafficher dans les lieux de travail o il sapplique, lemployeur tablissant un dcompte de la dure du travail, des repos compensateurs acquis et de leur prise effective, pour chacun des salaris concerns. Les dlgus du personnel peuvent consulter ces documents: article L. 3171-2 du code du travail.</p><p>Larticle suivant du mme code indique aussi que lemployeur tient  la disposition de lagent de contrle de linspection du travail comptent pour lentreprise, les documents permettant de comptabiliser le temps de travail accompli par chaque salari. En cas dhoraire collectif, un double est transmis  linspecteur du travail avant laffichage sur les lieux de travail: il en est de mme en cas de modification de celui-ci: article D. 3171-3 et D. 3171-4 du code dj cit.</p>","theme":"DURE DU TRAVAIL","branche":"MTALLURGIE","question":"Je suis salari (ouvrier) dans une entreprise du secteur Mtallurgie dans le dpartement du Rhne. Je fais actuellement 35h du lundi au vendredi (parfois quelques heures supplmentaires mais rcupres ou payes). Ci-joint mon contrat de travail.\nVendredi, on nous a informs que, maintenant, selon la charge de travail, on pourrait tre amens parfois  travailler le samedi. On nous a bien sr prcis que ces heures seraient des heures supplmentaires donc payes ou rcupres.\nLemployeur a-t-il le droit de nous imposer de travailler le samedi?"},{"reponse":"<p>Les heures effectues au-del de 35 heures par semaine, ds lors que lorganisation de votre temps de travail se fait dans le cadre de la semaine, constituent des heures supplmentaires. Elles doivent tre soit rmunres aux taux horaires majors dans les conditions prvues par l'accord d'entreprise ou  dfaut, par la convention collective nationale du btiment, soit 25% pour les 8 premires heures hebdomadaires et 50% au-del (article 3.17 de la convention collective).</p><p>Le paiement peut, le cas chant, tre remplac par un repos qui devra tre major dans les mmes conditions.</p><p>Vous pouvez consulter le texte des conventions collectives sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>:\nConvention collective nationale des ouvriers employs par les entreprises du btiment occupant jusqu 10 salaris: identifiant IDCC 1596</p><p>Convention collective nationale des ouvriers employs par les entreprises du btiment occupant plus de 10 salaris: identifiant IDCC 1597</p>","theme":"DURE DU TRAVAIL","branche":"BTIMENT","question":"Ouvrier dune entreprise de Btiment, je travaille rgulirement 42 heures par semaine pour un salaire mensuel de 151h67. Quelles sont les compensations dues en contrepartie de lexcution de ces heures supplmentaires?"},{"reponse":"<p>La rmunration affrente  ce jour doit tre maintenue par votre employeur. En effet, lentreprise tant ferme le jour fri, il nest pas considr comme un jour ouvrable et ne peut tre, de ce fait, dcompt comme un jour de congs pays.</p>","theme":"DURE DU TRAVAIL","branche":"BTIMENT","question":"Salari dans le secteur du BTP, durant la priode de fermeture de mon entreprise pour congs pays, le 15 aot est tomb un lundi, comment ce jour fri doit-il tre trait?"},{"reponse":"<p>Cette dure est de 12 heures pour le personnel roulant (article R. 3312-51 du code des transports)</p>","theme":"DURE DU TRAVAIL","branche":"TRANSPORTS ROUTIERS","question":"Quelle est la dure quotidienne maximale de travail dun chauffeur dans les transports routiers de marchandises?"},{"reponse":"<p>Les heures de prsence responsable sont celles pendant lesquelles le salari peut utiliser ce temps pour lui-mme,  condition de rester vigilant. Exemple: regarder la tlvision pendant la sieste des enfants.</p><p>Ces heures de prsence responsable doivent tre notes dans le contrat de travail. Il est prcis quune heure de prsence responsable quivaut aux 2/3 dune heure de travail effectif.</p><p>(Rfrences: postes d'emploi  caractre familial (PECF), article 3 b) de la Convention collective nationale des salaris du particulier employeur, que vous pouvez consulter sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a> Son code didentification (IDCC) est le 2111).</p>","theme":"DURE DU TRAVAIL","branche":"PARTICULIERS EMPLOYEURS","question":"Que signifie prsence responsable dans le contrat de travail?"},{"reponse":"<p> Vous pouvez tre en arrt maladie pendant le pravis de dmission; cette situation est sans incidence sur son droulement et sur sa date dexpiration. Ntant pas suspendu pendant votre absence,  le pravis continue  courir et se terminera  la date prvue.</p><p>Ds linstant o vous prvenez votre employeur, dans les 48 heures, et que vous justifiez votre absence par lenvoi, sous un dlai de 3 jours, dun certificat mdical prescrivant larrt de travail, votre employeur ne peut pas vous sanctionner pour votre absence (article 36 de la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales)).</p><p>Vous pouvez consulter cette convention collective sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"MALADIE","branche":"BOULANGERIES-PTISSERIES ARTISANALES","question":"J'occupe un poste de vendeuse en boulangerie-ptisserie et je viens de poser ma dmission. Quelles sont les consquences dun arrt maladie pendant la priode de pravis? Est-ce que je peux tre sanctionne en cas darrt de travail?"},{"reponse":"<p>Non, les priodes de maladie nouvrent pas droit  congs pays. Seuls les cas viss  larticle 12, 1, c/ de la convention collective nationale des assistants maternels du particulier employeur, comme le cong maternit par exemple sont assimils  de laccueil effectif pour la dtermination du nombre de jours de congs pays.</p><p>Vous pouvez consulter cette convention collective sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 2395.</p>","theme":"MALADIE","branche":"ASSISTANT.E.S MATERNEL.LE.S","question":"Je suis en arrt maladie ordinaire, est-ce que jacquiers des congs pays?"},{"reponse":"<p>Le principe pos par la jurisprudence est que lorsque la convention collective impose un maintien de salaire en cas de maladie, il convient de prendre en compte la partie variable de la rmunration ds lors que la convention ne l'exclut pas.</p><p>Dans le cas dune rmunration variable, le salaire qui aurait t gagn pendant la priode dabsence donnant lieu  indemnisation doit tre significatif au regard de labsence indemnise, ce qui peut conduire  retenir soit celui de la priode de paie prcdant cette absence, soit un salaire moyen peru au cours dune priode plus longue, le trimestre, par exemple.\nLarticle 6-04 de la convention collective du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes prvoit, par ailleurs, pour les salaris rmunrs par un fixe et des primes, une garantie de rmunration.</p><p>Ainsi, lorsque la rmunration comprend un fixe et des primes sur ventes, la partie fixe ne doit pas tre infrieure, pour un mois complet,  50% du salaire minimum conventionnel garanti applicable au salari, et la rmunration mensuelle totale ne doit pas tre infrieure au minimum garanti assorti des majorations prvues par l'article 1.09 en cas d'option pour l'un ou l'autre des forfaits individuels viss aux paragraphes d  g dudit article (situation des conventions de forfait).</p><p>Vous pouvez consulter la convention collective nationale du commerce et de la rparation de l'automobile, du cycle et du motocycle et des activits connexes, sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 1090.</p>","theme":"MALADIE","branche":"GARAGES","question":"Jai 10 ans danciennet et je suis en arrt maladie depuis 1 mois, quel impact cela va avoir sur la part variable de mon salaire?"},{"reponse":"<p>La convention collective des entreprises de propret prvoit  larticle 4.9.1 un mcanisme dit de garantie demploi permettant de protger le salari dune rupture de contrat en cas darrt de travail. Ce dispositif est fonction de lanciennet du salari et de la dure darrt:</p><table>\n<thead>\n<tr>\n<th align=\"center\">Dure de larrt de travail</th>\n<th>Anciennet</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">2 mois</td>\n<td>aprs la priode dessai et jusqu un an</td>\n</tr>\n<tr>\n<td align=\"center\">4 mois</td>\n<td>2 ans d'anciennet jusqu' 3 ans d'anciennet rvolus</td>\n</tr>\n<tr>\n<td align=\"center\">5 mois</td>\n<td>4 ans d'anciennet jusqu' 8 ans d'anciennet</td>\n</tr>\n<tr>\n<td align=\"center\">9 mois</td>\n<td> partir de 9 ans d'anciennet.</td>\n</tr>\n</tbody>\n</table><p>Lemployeur qui veut licencier un salari doit respecter la procdure de licenciement et ces dlais conventionnels.</p><p>Dans votre cas, cela est diffrent: vous me dites avoir fait une chute chez un client; Il devrait sagir dun arrt de travail suivant un accident de travail, sil a t dclar et reconnu comme tel. Le mme article de la convention collective prvoit quen cas de maladie professionnelle ou daccident du travail, lemployeur ne peut pas rompre le contrat de travail.</p><p>Vous pouvez consulter cette convention collective nationale des entreprises de propret et services associs sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 3043.</p>","theme":"MALADIE","branche":"ENTREPRISES DE PROPRET","question":"Cela fait plus de 8 mois que jai t arrt, aprs une chute chez un client, et mon mdecin veut encore prolonger mon arrt de travail. Jai lu sur internet, que mon patron pourrait me licencier, est-ce vrai? (jai 12 annes danciennet)"},{"reponse":"<p>Larticle L 1226-1 du code du travail dispose que tout salari ayant une anne danciennet dans lentreprise bnficie, en cas dabsence de travail justifie par lincapacit rsultant de maladie ou daccident constat par certificat mdical et contre visite sil y a lieu, dune indemnit complmentaire  lallocation journalire  prvue par larticle L 321-1 du code de la scurit sociale. Cette indemnit est calcule selon les modalits dfinies par lart D. 1226-1 du code du travail, soit 90% de la rmunration brute pendant 30 jours, puis 2/3 pendant  les 30 jours suivants.</p><p>Les textes conventionnels qui vont sont applicables (Mtallurgie: accords nationaux ouvriers, ETAM, ingnieurs et cadres - brochure JO 3109) prvoient des stipulations plus favorables sur lindemnisation de la maladie, que les dispositions lgales et rglementaires:</p><p>Aprs un an d'anciennet, en cas d'absence au travail justifie par l'incapacit rsultant de maladie ou d'accident dment constate par certificat mdical et contre-visite, s'il y a lieu, l'intress bnficiera des dispositions suivantes,  condition d'avoir justifi ds que possible de cette incapacit, d'tre pris en charge par la scurit sociale et d'tre soign sur le territoire mtropolitain ou dans l'un des autres pays de lUnion europenne.</p><ul>\n<li>Pendant quarante-cinq jours, le salari recevra la rmunration qu'il aurait perue s'il avait continu  travailler.</li>\n<li>Pendant les trente jours suivants, il recevra les trois quarts de cette mme rmunration.</li>\n</ul><p>Le premier temps d'indemnisation (quarante-cinq jours) sera augment de quinze jours par priode entire de cinq ans d'anciennet</p><p>Le deuxime temps d'indemnisation (trente jours) sera augment de dix jours par priode de mme dure.</p><p>La convention collective ne prvoit aucun dlai de carence avant dappliquer le versement de lindemnisation.\nLes garanties accordes s'entendent dduction faite des allocations que l'intress peroit des caisses de scurit sociale ou des caisses complmentaires, mais en ne retenant dans ce dernier cas que la part des prestations rsultant des versements patronaux.\nEn tout tat de cause ces garanties ne doivent pas conduire  verser  l'intress, compte tenu des sommes de toutes provenances perues  l'occasion de la maladie ou de l'accident du travail, un montant suprieur  la rmunration nette qu'il aurait effectivement perue s'il avait continu de travailler sous dduction de la rmunration correspondant au dlai de franchise.</p><p>La rmunration  prendre en considration est celle correspondant  l'horaire pratiqu, pendant son absence, dans l'tablissement ou partie d'tablissement, sous rserve que cette absence n'entrane pas une augmentation de l'horaire pour le personnel restant au travail.\nLa prsence prise en compte pour la dtermination du droit  indemnisation s'apprcie au premier jour de l'absence. Toutefois, si un travailleur qui n'a pas l'anciennet voulue pour bnficier des dispositions du prsent article acquiert cette anciennet pendant qu'il est absent pour maladie ou accident, il lui sera fait application desdites dispositions pour la priode d'indemnisation restant  courir. Attention: si plusieurs congs de maladie donnant lieu  indemnisation au titre du prsent article sont accords au cours d'une anne civile, la dure d'indemnisation ne peut excder au total celle des priodes ci-dessus fixes.\nL'indemnisation calcule conformment aux dispositions ci-dessus interviendra aux dates habituelles de la paie.</p><p>Jattire votre attention sur le fait que les dispositions ci-dessus, prvues par les accords nationaux de la mtallurgie, sappliquent  dfaut daccord territorial (rgional ou dpartemental) de la mtallurgie ou daccord dentreprise plus favorables.</p><p>Je vous rappelle que la majorit des conventions collectives peuvent tre consultes sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a></p>","theme":"MALADIE","branche":"MTALLURGIE","question":"Je suis ouvrier dans le secteur de la mtallurgie et je suis actuellement en arrt maladie. Je perois des indemnits journalires verses par la CPAM, lemployeur doit-il me verser un complment de salaire?"},{"reponse":"<p>Oui, vous devez lui proposer un autre emploi appropri  ses capacits, au sein de votre entreprise ou des entreprises du groupe auquel elle appartient le cas chant, situes sur le territoire national et dont lorganisation, les activits ou le lieu dexploitation assurent la permutation de tout ou partie du personnel (article L. 1226-10 du code du travail).\nPour cela vous devez prendre en compte les conclusions crites du mdecin du travail et les indications quil formule sur les capacits du salari  exercer lune des tches existant dans lentreprise.</p><p>Le mdecin du travail pourra formuler galement des indications sur laptitude du salari  bnficier dune formation le prparant  occuper un poste adapt.\nBien entendu, vous pourrez solliciter le mdecin pour recueillir son avis.\nLemploi propos devra tre aussi comparable que possible  lemploi prcdemment occup, au besoin par la mise en uvre de mesures telles que mutations, amnagements, adaptations ou transformations de postes existants ou amnagement du temps de travail.</p>","theme":"MALADIE","branche":"COMMERCES  PRDOMINANCE ALIMENTAIRE","question":"Une de mes caissires, victime dune maladie professionnelle,  a t dclare inapte par le mdecin du travail  son poste, dois je lui en proposer un autre?"},{"reponse":"<p>Laccident du travail est celui qui, quelle qu'en soit la cause, celui survenu au salari par le fait ou  l'occasion du travail en quelque lieu que ce soit. Peut galement tre considr comme accident du travail celui intervenu entre le domicile du salari et son lieu de travail (articles L. 411-1 et L. 411-2 du code de la scurit sociale).</p><p>La victime dun accident du travail doit en informer ou faire informer dans les 24 heures son employeur. Celui-ci doit alors transmettre  la caisse primaire dassurance maladie une dclaration daccident dans un dlai de 48 heures (articles L. 441-1 et suivants et R. 441-1 et suivants du code de la scurit sociale).</p><p>En cas de refus dun employeur deffectuer cette dclaration, le salari peut dans un premier rappeler  son employeur son obligation par un courrier par lettre recommande avec accus de rception et en adresser une copie  la CPAM et  linspection du travail dont il relve. En cas de refus persistant de lemployeur, le salari peut lui-mme effectuer cette dclaration dans un dlai de deux ans qui suit laccident (article L. 441-2 du code de la scurit sociale).</p>","theme":"MALADIE","branche":"BTIMENT","question":"Ouvrier dune entreprise du btiment, jai t victime dun accident sur le chantier auquel jtais affect. Mon employeur na pas fait de dclaration daccident du travail. Comment-faire pour que je puisse tre indemnis au titre dun accident du travail?"},{"reponse":"<p>Les pourcentages d'indemnisation s'appliquent sur la base de la rmunration qui aurait t perue si ce personnel avait continu  travailler.</p><p>Au terme des articles 10 ter de l'annexe I et 17 bis de l'annexe II de la convention collective des transports routiers et des activits auxiliaires du transport (IDCC 16), les ouvriers et employs doivent bnficier du maintien de salaire en cas d'arrt de travail suivant:</p><p>En cas de maladie:</p><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet</th>\n<th align=\"center\">Dure du maintien  100%</th>\n<th align=\"center\">Dure du maintien  75%</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">Aprs 3 ans</td>\n<td align=\"center\">du 6me au 40me jour</td>\n<td align=\"center\">du 41me au 70me jour</td>\n</tr>\n<tr>\n<td align=\"center\">Aprs 5 ans</td>\n<td align=\"center\">du 6me au 70me jour</td>\n<td align=\"center\">du 71me au 130me jour</td>\n</tr>\n<tr>\n<td align=\"center\">Aprs 10 ans</td>\n<td align=\"center\">du 6me au 100me jour</td>\n<td align=\"center\">du 101me au 190me jour</td>\n</tr>\n</tbody>\n</table><p>(*)En cas d'hospitalisation les priodes d'indemnisation  75% vises ci-dessus sont prolonges de 30 jours.</p><p>En cas d'accident du travail:</p><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet</th>\n<th align=\"center\">Dure du maintien  100%</th>\n<th align=\"center\">Dure du maintien  75%</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">Aprs 3 ans</td>\n<td align=\"center\">du 1er au 30me jour</td>\n<td align=\"center\">du 31me au 90me jour</td>\n</tr>\n<tr>\n<td align=\"center\">Aprs 5 ans</td>\n<td align=\"center\">du 1er au 60me jour</td>\n<td align=\"center\">du 61me au 150me jour</td>\n</tr>\n<tr>\n<td align=\"center\">Aprs 10 ans</td>\n<td align=\"center\">du 1er au 90me jour</td>\n<td align=\"center\">du 91me au 210me jour</td>\n</tr>\n</tbody>\n</table><p>Vous pouvez consulter la convention collective des transports routiers et activits auxiliaires du transport sur le site <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a>; son code didentification (IDCC) est 16.</p>","theme":"MALADIE","branche":"TRANSPORTS ROUTIERS","question":"Quel est mon maintien de salaire en maladie?"},{"reponse":"<p>Selon larticle 11 bis de la convention collective nationale des transports routiers et activits auxiliaires du transport:\nPour le personnel de conduite justifiant au moins de 1 anne d'anciennet dans l'entreprise, le cot des visites mdicales obligatoires en vue du renouvellement de la validit du permis de conduire et la perte de salaire effectif rsultant du temps pass auxdites visites sera galement pris en charge par l'entreprise.</p><p>Vous pouvez consulter la convention collective des transports routiers et activits auxiliaires du transport sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est 16.</p>","theme":"MALADIE","branche":"TRANSPORTS ROUTIERS","question":"Qui doit payer le cot des visites mdicales poids lourds?"},{"reponse":"<p>Selon larticle 29 de la convention collective nationale des htels, cafs, restaurants, lindemnisation complmentaire intervient ds le premier jour dabsence, en cas daccident du travail ou de maladie professionnelle.</p><p>En cas de maladie ordinaire, daccident non professionnel ou daccident de trajet, elle dbute  compter du 8 jour.</p><p>Le salari peroit un pourcentage de la rmunration brute (y compris les indemnits journalires de la scurit sociale) quil aurait gagne sil avait continu  travailler, et ce, dans les conditions suivantes:</p><ul>\n<li>pendant les 30 premiers jours, 90% de cette rmunration;</li>\n<li>pendant les 30 jours suivants, deux tiers de cette mme rmunration.</li>\n</ul><p>Ces dures dindemnisation sont augmentes de 10 jours par priode entire de 5 ans danciennet en plus de la dure dune anne ans requise pour pouvoir prtendre  cette indemnisation complmentaire, sans que chacune delle puisse dpasser 90 jours.</p><p>Le complment de rmunration d par l'employeur s'entend dduction faite des indemnits que l'intress peroit de la scurit sociale et le cas chant des rgimes complmentaires de prvoyance.</p><p>Vous pouvez consulter la convention collective des htels, cafs, restaurants sur le site <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a>; son code didentification (IDCC) est le 1979.</p>","theme":"MALADIE","branche":"HOTELS-CAFS-RESTAURANTS","question":"Que peroit le salari en arrt maladie?"},{"reponse":"<p>Oui, la convention collective du particulier employeur prvoit une surveillance mdicale pour tout type de contrat de travail (temps partiel et temps complet  CDD/CDI).\n(Rfrences: article L. 7221-2 du code du travail, article 22 de la convention collective nationale des salaris du particulier employeur que vous pouvez consulter sur le site <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a> avec le code didentification (IDCC) 2111).</p><p>Il existe par ailleurs un accord-cadre interbranches du 24 novembre 2016, tendu, relatif aux rgles d'organisation et au choix du service de sant au travail, au suivi individuel et collectif et  la prvention de l'altration de la sant des travailleurs pour les salaris des particuliers employeurs, que vous pouvez consulter dans les mmes conditions.</p>","theme":"MALADIE","branche":"PARTICULIERS EMPLOYEURS","question":"La visite mdicale auprs dun service de sant au travail est-elle obligatoire?"},{"reponse":"<p>La convention collective nationale de la boulangerie - ptisserie (entreprises artisanales) prvoit que lemployeur doit aux salaris ayant plus de deux annes d'anciennet, ce qui semble tre votre cas, deux mois de pravis en cas de licenciement.</p><p>De plus, cette convention collective accorde au salari, pour rechercher un nouvel emploi, deux heures libres par jour au cours de la dernire semaine de pravis. Ces deux heures, rmunres, seront prises alternativement au choix de l'employeur et du salari; toutefois, un accord peut intervenir permettant, entre autres, de grouper tout ou partie de ces heures (article 32 de la convention collective).\nLemployeur peut dispenser le salari dexcuter tout ou partie du pravis.\nEn ce qui vous concerne, la proposition de rduire dun mois le pravis qui vous est d, ne constituera pas, si vous lacceptez, une dispense  linitiative de votre employeur, mais une dcision prise d'un commun accord entre les deux parties.</p><p>Ceci a pour consquence quaucune indemnit compensatrice de pravis dite encore indemnit de brusque rupture ne sera obligatoirement verse par l'employeur.</p><p>Par contre, si vous refusez sa proposition et que votre employeur est dsireux de se sparer de vous un mois plus tt, il devra dcider seul de vous dispenser deffectuer une partie du pravis et devra vous verser cette indemnit compensatrice qui se cumule avec lindemnit de licenciement et qui correspond aux salaires et avantages, y compris l'indemnit de cong pay, que vous auriez perus si vous aviez travaill pendant cette priode (article L. 1234-5 du code du travail).</p><p>Il est donc conseill de vous faire confirmer par crit par lemployeur que linexcution partielle du pravis donnera lieu, y compris si vous lacceptez, au versement de cette indemnit.</p><p>En raison de lexistence, dans certains dpartements, dune annexe  la convention collective nationale de la boulangerie - ptisserie (entreprises artisanales), vous tes invit  consulter celle qui intresse ventuellement votre lieu de travail et qui peut contenir des dispositions particulires en matire de pravis de licenciement.</p><p>Vous pouvez accder gratuitement  la convention collective applicable  votre employeur ainsi qu ses annexes dpartementales sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 843.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"BOULANGERIES-PTISSERIES ARTISANALES","question":"Je suis licencie par mon nouvel employeur qui a repris la boulangerie-ptisserie. Il na pas besoin dune vendeuse compte tenu que sa femme travaille au magasin. Il propose que le pravis de licenciement se limite  un mois au lieu de deux afin que je sois libre plus tt. Dois-je accepter sans rien perdre de mes droits, dautant que je nai pas dautre emploi en vue?"},{"reponse":"<p>Le droit de retrait peut sexercer librement par lemployeur en application de larticle 18 de la convention collective nationale des assistants maternels, il ne sagit pas dune procdure de licenciement. La rupture du contrat de travail qui rsulte du retrait de lenfant accueilli na pas  tre motive.</p><p>Cependant, le motif ne devra pas tre discriminatoire. Est discriminatoire, la rupture du contrat de travail qui intervient notamment:</p><ul>\n<li>en raison de ltat de sant du salari,</li>\n<li>en raison de ltat de grossesse de la salarie,</li>\n<li>de son ge,</li>\n<li>de ses origines,</li>\n<li>de son handicap,</li>\n<li>de son appartenance syndicale,\n(article L. 1132-1 du code du travail).</li>\n</ul><p>De plus, en cas de grossesse, de maternit ou aprs les 10 semaines suivant le cong de maternit (article L. 1225-4 du code du travail), la rupture du contrat de travail ne peut tre opre quen cas de faute grave du salari ou dimpossibilit de maintenir le contrat. Pendant le cong de maternit, aucune rupture du contrat nest possible.</p><p>Vous pouvez consulter la convention collective nationale des assistants maternels du particulier employeur sur le site <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a>; son code didentification (IDCC) est le 2395.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"ASSISTANT.E.S MATERNEL.LE.S","question":"Je suis employeur dun assistant maternel, dois-je motiver la lettre de retrait de mon enfant?"},{"reponse":"<p>Le retrait forc du fait de la suspension de lagrment simpose au salari et  lemployeur. Par consquent, le contrat de travail doit tre rompu par lemployeur par lettre recommand avec avis de rception  la date de notification de la suspension ou du retrait dagrment par le Conseil dpartemental. Aucune indemnit de pravis ou de rupture nest due  lassistant maternel (article L. 423-24 du code de laction sociale et des familles). Lemployeur sera redevable uniquement de lindemnit compensatrice de congs pays.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"ASSISTANT.E.S MATERNEL.LE.S","question":"Lagrment de lassistant maternel qui accueille mon enfant est suspendu, que dois-je faire?"},{"reponse":"<p>Larticle L. 1234-1 du code du travail prvoit effectivement le droit  un pravis en cas de licenciement. Ce dernier nest toutefois pas d en cas de faute grave.\nLa dure du pravis est fixe par la loi  larticle L. 1234-1 du code du travail comme suit:</p><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet (service continu)</th>\n<th align=\"center\">Dure du pravis</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">&#x3C; 6 mois</td>\n<td align=\"center\">~</td>\n</tr>\n<tr>\n<td align=\"center\">6 mois  deux ans</td>\n<td align=\"center\">1 mois</td>\n</tr>\n<tr>\n<td align=\"center\"> 2 ans</td>\n<td align=\"center\">2 mois</td>\n</tr>\n</tbody>\n</table><p>Le contrat de travail ne pourra prciser une dure infrieure  ces dispositions lgales.</p><p>La dure du pravis est galement prcise par ce mme article, mais galement, dans votre cas, par larticle 4.11.2 de la convention collective nationale des entreprises de propret. La dure du pravis est ainsi module en fonction de votre anciennet de votre catgorie de personnels (une distinction est faite entre les personnels agents de propret, employ, technicien et agents de matrise et cadre).</p><h5>Personnel agent de propret</h5><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet (service continu)</th>\n<th align=\"center\">Dure du pravis</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">de 1  6 mois</td>\n<td align=\"center\">1 semaine</td>\n</tr>\n<tr>\n<td align=\"center\">de 6 mois  2 ans</td>\n<td align=\"center\">d'anciennet  1 mois</td>\n</tr>\n<tr>\n<td align=\"center\">plus de 2 ans</td>\n<td align=\"center\">2 mois</td>\n</tr>\n</tbody>\n</table><h5>Personnel employ</h5><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet (service continu)</th>\n<th align=\"center\">Dure du pravis</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">de 1 mois  2 ans</td>\n<td align=\"center\">1 mois</td>\n</tr>\n<tr>\n<td align=\"center\">plus de 2 ans</td>\n<td align=\"center\">2 mois</td>\n</tr>\n</tbody>\n</table><h5>Personnel technicien et agent de matrise</h5><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet (service continu)</th>\n<th align=\"center\">Dure du pravis</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">de 1 mois  2 ans</td>\n<td align=\"center\">1 mois</td>\n</tr>\n<tr>\n<td align=\"center\">plus de 2 ans</td>\n<td align=\"center\">2 mois</td>\n</tr>\n</tbody>\n</table><h5>Personnel cadre</h5><table>\n<thead>\n<tr>\n<th align=\"center\">Anciennet (service continu)</th>\n<th align=\"center\">Dure du pravis</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\"> l'expiration de la priode d'essai</td>\n<td align=\"center\">3 mois</td>\n</tr>\n</tbody>\n</table><p>La convention collective prvoit galement un droit  temps de recherche demploi au cours du pravis (art. 4.11.1).</p><p>Concrtement, le salari a le droit  deux heures par jours pour rechercher un emploi sil est  temps plein, et proportionnellement autant sil est  temps partiel. Sagissant dun licenciement, ces heures de recherche d emploi sont rmunres par lemployeur. La Convention collective nationale des entreprises de propret du 26 juillet 2011 prcise que ces heures pourront tre regroupes en fin de pravis ou prises chaque jour 1 fois au choix du salari, 1 fois au choix de l'employeur.</p><p>Lemployeur qui ne respecte pas le pravis doit verser une indemnit de pravis gale  la rmunration du prvis non effectu (art. 4.11.1).</p><p>Vous pouvez consulter cette convention collective nationale des entreprises de propret et services associs sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 3043.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"ENTREPRISES DE PROPRET","question":"Je travaille dans une entreprise de nettoyage de locaux. Jai reu ma convocation pour un entretien de licenciement, quels sont mes droits pour le pravis?"},{"reponse":"<p>La convention collective des entreprises de la propret (IDCC 3043, disponible sur Lgifrance) ne contient pas de stipulations relatives aux ventuelles clauses de non-concurrence.</p><p>Toutefois, je vous invite  vrifier si votre contrat de travail ne contient pas une telle clause,  laquelle vous seriez alors astreint sous rserve que celle-ci soit licite.</p><p>En l'absence de dispositions lgales, cest la jurisprudence qui a dfini les conditions de validit des clauses de non-concurrence.</p><p>Ainsi, la Cour de cassation subordonne la validit d'une clause de non-concurrence au respect de cinq conditions:</p><ul>\n<li>tre indispensable  la protection des intrts lgitimes de l'entreprise,</li>\n<li>tre limite dans le temps,</li>\n<li>tre limite dans l'espace,</li>\n<li>tenir compte des spcificits de l'emploi du salari,</li>\n<li>comporter pour l'employeur l'obligation de verser au salari une contrepartie financire.</li>\n</ul><p>(Cass. soc., 10 juill. 2002, n 99-43.334; Cass. soc., 10 juill. 2002, n 00-45.135, Cass. soc., 10 juill. 2002, n00-45.387, Cass. soc., 18 sept. 2002, n 00-42.904).\nCes conditions sont cumulatives. L'absence de l'une d'entre elles entrane la nullit de la clause.\nVous pouvez consulter la convention collective nationale des entreprises de propret et services associs sur le site <a href=\"https://www.legifrance.gouv.fr\">https://www.legifrance.gouv.fr</a>; son code didentification (IDCC) est le 3043.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"ENTREPRISES DE PROPRET","question":"Je suis laveur de vitres, et dmissionne de mon entreprise. Suis-je astreint  une clause de non-concurrence?"},{"reponse":"<p>Laccord national du 10 juillet 1970 modifi relatif  la mensualisation dans la mtallurgie prcise dans son article 16 que pour chaque sujet trait, il se substitue, sil est plus favorable,  lensemble des clauses relatives au mme sujet, des avenants ou conventions collectives applicables aux ouvriers.\nPar dcision en date du 7 fvrier 2012 (n 09-70130), la Cour de cassation sest prononce sur la question:\nPour la Cour, il y a donc bien une situation de concours qui doit se rgler par lapplication de la disposition la plus favorable aux salaris. En lespce, les salaris classs aux niveaux IV et V de la classification de la convention collective dpartementale doivent donc se voir appliquer le pravis de dmission de 2semaines prvu par laccord national sur la mensualisation, plus favorable que les pravis de 2 et 3 mois prvus par la convention collective dpartementale.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"MTALLURGIE","question":"Je travaille dans une entreprise de mtallurgie de la Drme et je suis class au niveau IV. Je vais dmissionner en ne respectant que 2 semaines de pravis comme le prvoit laccord national sur la mensualisation dans la mtallurgie. Mon employeur nest pas daccord et me dit que je dois respecter le pravis de 2 mois prvu dans la convention collective de la mtallurgie Drme-Ardche. Suis-je dans mon droit?"},{"reponse":"<p>Il vous est en principe interdit de rompre son contrat pendant sa grossesse et pendant lintgralit des priodes de suspension auxquelles elle a droit conformment aux articles L. 1225-17  L. 1225-23 du code du travail, ainsi que pendant les 4 semaines qui suivent lexpiration de ces priodes (article 7-6.4.1 de la convention collective du commerce de dtail et de gros  prdominance alimentaire).</p><p>Il ne sera possible de procder  la rupture de son contrat quen cas de faute de lintresse ou de limpossibilit o vous vous trouvez, de maintenir son contrat pour un motif tranger  la grossesse ou  laccouchement. Dans ce cas la rsiliation du contrat ne pourra prendre effet ou tre signifie pendant la priode suspension de son contrat rsultant du cong maternit.</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"COMMERCES  PRDOMINANCE ALIMENTAIRE","question":"Je suis dirigeant dun supermarch, puis je rompre le contrat de travail de ma salarie qui se trouve en tat de grossesse mdicalement constate?"},{"reponse":"<p>Non, la carte de conducteur est demande par le conducteur lui-mme, mme sil appartient  lentreprise de faire les dmarches ncessaires.</p><p>La carte de conducteur est personnelle donc le conducteur garde sa carte mme sil dmissionne de lentreprise qui la lui a fournie (articles 26 et 27 du Rglement UE 165/2014 du fvrier 2014)</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"TRANSPORTS ROUTIERS","question":"Je vais quitter mon entreprise de transports routiers, est-ce que je dois rendre ma carte conducteur  mon patron?"},{"reponse":"<p>Le pravis de dmission diffre suivant la qualification. Il est de:</p><ul>\n<li>8 jours pour les employs ayant moins de 6 mois danciennet, de 15 jours pour ceux ayant entre 6mois et moins de 2 ans danciennet, et d1 mois pour ceux ayant 2 ans danciennet et plus,</li>\n<li>15 jours pour les agents de matrise ayant moins de 6 mois danciennet, d1 mois pour ceux ayant entre 6 mois et moins de 2 ans danciennet, et de 2 mois pour ceux ayant 2 ans danciennet et plus.</li>\n<li>1 mois pour les cadres ayant moins de 6 mois danciennet, et 3 mois pour ceux ayant 6 mois danciennet et plus.</li>\n</ul><p>(Article 30.1 de la convention collective nationale des htels, cafs, restaurants, que vous pouvez consulter sur le site <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a> avec son numro didentification (IDCC) 1979).</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"HOTELS-CAFS-RESTAURANTS","question":"Dans les HCR, quel est le pravis de dmission?"},{"reponse":"<p>La convention collective prvoit que le contrat ne se poursuit pas automatiquement avec les hritiers: le contrat est rompu  la date du dcs de lemployeur (la procdure de licenciement doit tre respecte).</p><p>(Rfrences: Dcs de lEmployeur, article 13 de la convention collective nationale des salaris du particulier employeur, consultable sur le site internet <a href=\"https://legifrance.gouv.fr\">https://legifrance.gouv.fr</a>; son numro didentification (IDCC) est le 2111).</p>","theme":"RUPTURE DU CONTRAT DE TRAVAIL","branche":"PARTICULIERS EMPLOYEURS","question":"Que se passe-t-il pour les salaris du particulier employeur, en cas de dcs de celui-ci?"}]

